import common from '../../common/common.js'
export default {
	data() {
		return {
			phones: '',
			codes: ''
		}
	},
	onLoad(options) {
		var names = uni.getStorageSync('data');
		if (names != '') {
			uni.switchTab({
				url: '../index/index'
			})
		}
	},
	methods: {
		// 获取input值
		phone(e) {
			var mobile = e.detail.value;
			this.phones = mobile;
		},
		code(e) {
			var yzm = e.detail.value;
			this.codes = yzm;
		},
		goLogin() {
			// if (common.regular(1, this.phones) == false) {
			// 	uni.showToast({
			// 		title: '手机号输入有误',
			// 		icon: 'none'
			// 	})
			// 	return false;
			// }
			var that = this;
			var data = {
				account: that.phones,
				password: that.codes
			}
			common.getData('/muqiang/invitation/userLogin', data, (res) => {
				if (res.state == 0) {
					uni.showToast({
						title: '账号或密码输入有误',
						icon: 'none'
					})
				} else{
					uni.setStorageSync('data', res.name);
					uni.setStorageSync('mobile', res.mobile);
					uni.setStorageSync('status', res.roles);
					uni.switchTab({
						url: '../index/index'
					})
				}
			})
		}
	}
}
